import React from "react";
import PropTypes from "prop-types";

const Title = ({ level, isWhite, isCapitalized, content }) => {
	let classes = [ "title" ];

	if (isWhite) {
		classes.push("title--white");
	}

	if (isCapitalized) {
		classes.push("title--capitalized");
	}

	const classesStr = classes.join(" ");

	switch (level) {
		case 1:
			return <h1 className={classesStr + " title--primarySize"}>{content}</h1>;
		case 2:
			return <h2 className={classesStr}>{content}</h2>;
		case 3:
			return <h3 className={classesStr}>{content}</h3>;
		default:
			return null;
	}
};

Title.propTypes = {
	level: PropTypes.number,
	isWhite: PropTypes.bool,
	isCapitalized: PropTypes.bool,
	content: PropTypes.string.isRequired
};

Title.defaultProps = {
	level: 2
};

export default Title;
